@import './../../../styles/base';
.gradiant-button{
  background: linear-gradient(to right, $secondary-color-dark, $secondary-color);
  color: $copy-color;
  padding: .4rem .5rem;
  box-sizing: border-box;
  font-weight: 700;
    font-size: 1.4rem;
  transition: all .2s ease-in-out;
  user-select: none;
  &:hover{
    background: linear-gradient(to right, $secondary-color, $secondary-color-dark);
    transform: scale(1.1);
    cursor: pointer;
  }
}

.red-variant{
  background: linear-gradient(to right, $error-color, $error-color-light) !important;
  &:hover{
    background: linear-gradient(to right, $error-color-light,$error-color) !important;
  }
}