.leaderboard-page{
  &__body{
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 4rem;
    box-sizing: border-box;
    @media only screen and (max-width: 1600px) {
      padding: 2rem;
    }
    &__leaderboard-box{
      display: flex;
      width: 100%;
      max-height: 50rem;
      gap:2rem;
        @media only screen and (max-width: 1600px) {
            max-height: 30rem;
        }
    }
    &--title{
      font-size: 5rem !important;
    }
  }
}