@import '../../../styles/base.scss';
.encrypt-button{
  background-color: $primary-color-light;
  height: 7rem;
  border: none;
  display: flex;
  margin: 0;
    justify-content: center;
    align-items: center;
  padding: 0rem 2rem;
  box-sizing: border-box;
  &:hover{
    cursor: pointer;
  }
  &--text{
    font-family: $primary-font;
    font-size: 4.5rem;
    font-weight: 700;
    color: $primary-color-content;
  }
}
.button-wrapper{
  border: none !important;
  padding: 0rem !important;
}