@import './../../../styles/base.scss';
.profile{
  overflow: hidden;
  &__body{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100%;
    width: 100%;
    padding: 2rem;

    gap: 2rem;
    box-sizing: border-box;
    background-color: $background-color;
    &__overview{
      height: 6rem;
        width: 100%;
      background-color: $primary-color;
      display: flex;
        align-items: center;
      justify-content: space-around;
      &--title{
        font-size: 2rem;
        padding: 1rem 2rem;
        box-sizing: border-box;
        background-color: $foreground-color;
      }
    }
    &__main{

      width: 100% ;
      //is controlled by main-wrapper
        height: 100%;
       display:grid;
         grid-template-columns: 1fr 2fr;
         grid-template-rows: 1fr 1fr;
          grid-gap: 2rem;
      grid-template-areas:
      "public-winrate game-history"
      "private-winrate game-history";
      @media (max-width: 1000px){
        height: 100rem;
        grid-template-columns: 1fr;
        grid-template-rows: 1fr 1fr 1fr 1fr;
        grid-template-areas:
        "public-winrate"
        "private-winrate"
        "game-history"
        "game-history";
      }
      &__winrate-box{
        display: flex;
        flex-direction: column;
        gap:.5rem;
        align-items: center;
        background-color: $primary-color;

        &--title{

        }
        &--graph{
          width: 100%;
          height: 100%;
        }
        &--private{
          grid-area: private-winrate;
        }
        &--public{
          grid-area: public-winrate;
        }
      }

    }
  }
}
.main-wrapper{
  width: 100% ;
  height: 50rem;
    position: relative;
  overflow: hidden;
  &__no-games-overlap{
    position: relative;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.2); // Change this to the color you want
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: white; // Change this to the color you want
    text-align: center;
    z-index: 1;
  }
}