@import './../../styles/base.scss';
.hero-moving{
    position: relative;
    width: 100%;
    height: 40rem;
    background-color: $background-color;
  @media screen and (max-width: 1600px) {
    height: 30rem;
  }
  &__bg-text{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    font-size: 10rem;
    font-weight: 900;
    background-color: $foreground-color;
    display: flex;
    flex-direction: column;
    @media screen  and (max-width: 1600px) {
      font-size: 7rem;
    }

    &__line{
      display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
      width: fit-content;
      gap: 5rem;
      overflow: hidden;
      &--1{
        animation: moveLeft 30s linear infinite;
      }
        &--2{
            animation: moveRight 30s linear infinite;
        }
      &--3{
        animation: moveLeft 30s linear infinite;
      }
      &--moveRightSlower{
        animation: moveRightSlower 30s linear infinite !important;
      }


      &__text{
        text-wrap: none;
        white-space: nowrap;
        font-size: 20rem !important;
        color:$border-color;
        user-select: none;
        @media screen  and (max-width: 1600px) {
          font-size: 15rem !important;
        }
        &:hover{
          color: $primary-color-content !important;
        }

      }
      &__img{
        width: 30rem;
        height: 30rem;

      }
    }
  }

}
@keyframes moveRightSlower {
  0% {
    transform: translateX(-40%);
  }
  100% {
    transform: translateX(60%); // Move the text to the left by half of its width
  }
}
@keyframes moveLeft {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(-50%); // Move the text to the left by half of its width
  }
}
@keyframes moveRight {
    0% {
        transform: translateX(-100%);
    }
    100% {
        transform: translateX(60%); // Move the text to the right by half of its width
    }
}