@import "../../../styles/base.scss";
@import './../../../styles/mixins.scss';
.secondary-input-container{
  flex-grow:0;
  position: relative;
  display: flex;
  align-items: center;
  flex-direction: column;
  gap: .1rem;
  width: 100%;
  height: fit-content;
  //top: 50%;
  background-color: $foreground-color;
  padding:0rem 4rem 5rem 5rem;
  box-sizing: border-box;
  @media only screen and (max-width: 1600px) {
    width: 70rem;
    margin-left: 5rem;
    padding: 0rem 2rem 2rem 2rem;
  }
  @media only screen and (max-width: 1200px) {
    width: 100%;
    padding: 0rem 2rem 2rem 2rem;
    margin-left: 0;
  }
  &--heading{
    font-size: 7rem;
    font-weight: 900;
    color: $secondary-color;
    word-wrap: break-word;
    @media only screen and (max-width: 1600px) {
      font-size: 5rem;
    }
    @media only screen and (max-width: 1200px) {
      font-size: 4rem;
    }
  }
  &__paragraph-box{

  }
  &--paragraph{
    font-size: 2rem;
    font-weight: 900;
    color: $code-light-color;

  }
  &--input{
    @include DefaultInput;
  }
}