@import './../../styles/base';
.feature-card{
  width: 32rem;
    height: 15.5rem;
  padding: 2rem;
  box-sizing: border-box;
    background-color: $border-color;

  &__header{
    display: flex;
    gap:1rem;
    align-items: center;
    &--text{
      font-size: 2rem;
      font-weight: 600;
      color: $copy-color;
      @media only screen and (max-width: 1600px) {
        font-size: 1.5rem;
      }
    }
    &--img{
        width: 5rem;
        height: 5rem;
      object-fit: contain;
      margin-left: auto;
      @media only screen and (max-width: 1600px) {
        width: 4rem;
        height: 4rem;
      }
    }

  }
  &__subheader{
    width: 100%;
    height: fit-content;
    &--text{
        font-size: 1.1rem;
        font-weight: 400;
        color: $copy-color;
      @media only screen and (max-width: 1600px) {
        font-size: 1rem;
      }

    }
  }

}
