@import './../../../styles/base.scss';
.leaderboard{
  width: 100%;
  display: flex;
  flex-direction: column;
    align-items: center;
  max-height: 50rem;
  &__body{
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    overflow-y: scroll;
    gap: 2rem;
    padding: 2rem;
    box-sizing: border-box;
    background-color: $primary-color;

    &__contestant-box{
      display: flex;
      gap:2rem;
      width: 100%;
      height: 5rem;
      padding: 1rem;
      box-sizing: border-box;
      //justify-content: space-around;
        align-items: center;
      background-color: $foreground-color;
      &:hover{
        //scale up
        transform: scale(1.05);
        transition: transform .3s;
          cursor: pointer;
      }
      &__left{
        display: flex;
        gap:.5rem;
        justify-self: flex-start;
        width: fit-content;
        //move self to left
        align-items: center;
        &--username{

        }
        &--img{
            width: 5rem;
            height: 5rem;
        }
      }
      &__center{
        display: flex;
        gap:.5rem;
        width:100%;
        //justify-self: center;
        justify-content: space-around;
      }

      &--value{

      }
    }

  }
}
