@import './base.scss';
@mixin DefaultPage {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap:0rem;
}
@mixin DefaultInput{
    width: 100%;
    height: 5rem;
    padding: 1rem;
    box-sizing: border-box;
    font-size: 2rem;
    font-weight: 900;
    color: $code-light-color;
    background-color: $foreground-color;
    border: 5px solid $code-light-color;
  &:focus{
        outline: none;
        border: 5px solid $secondary-color;
    }
}