@import './../../styles/base';
.theme-vote-button{
  display: flex;
  width: 100%;
  height: 4rem;
  padding: 0 1rem;
    box-sizing: border-box;
  justify-content: space-between;
    align-items: center;
  background-color: $primary-color;
  user-select: none;
  &:hover{
    background-color: $primary-color-dark;
    transform: translateY(-2px);
  }
}